import React, { Component } from 'react';
// type
import {
    IPageflowDetailProps
} from "./IPageflow_detail";
// css
const Style = require("./Pageflow_style.module.css");
// shared component
import Taskbar from 'component/taskbar/Taskbar';
import { Title } from 'component/title/Title.jsx';
import {
    StyledGrid,
    StyledTitleGridBorder,
    StyledGridBorder,
    GridProportion,
} from 'component/material/StyledGrid';

export class PageflowDetail extends Component<IPageflowDetailProps> {

    render() {

        const { editDataItem, handleHomeClick } = this.props;

        return (
            <div>
                <Taskbar
                    className={Style.taskbararea}
                    data={[
                        { name: '返回', onClick: handleHomeClick() },
                    ]}
                />

                <Title title="範例明細" />
                <StyledGrid
                    container={true}
                >
                    <StyledTitleGridBorder
                        item={true}
                        sm={GridProportion.sm.titlecols}
                        xs={GridProportion.xs.titlecols}
                    >
                        <p>代碼</p>
                    </StyledTitleGridBorder>
                    <StyledGridBorder
                        item={true}
                        sm={GridProportion.sm.contentcols}
                        xs={GridProportion.xs.contentcols}
                    >
                        <p>{editDataItem.id}</p>
                    </StyledGridBorder>

                    <StyledTitleGridBorder
                        item={true}
                        sm={GridProportion.sm.titlecols}
                        xs={GridProportion.xs.titlecols}
                    >
                        <p>名字</p>
                    </StyledTitleGridBorder>
                    <StyledGridBorder
                        item={true}
                        sm={GridProportion.sm.contentcols}
                        xs={GridProportion.xs.contentcols}
                    >
                        <p>{editDataItem.name}</p>
                    </StyledGridBorder>

                    <StyledTitleGridBorder
                        item={true}
                        sm={GridProportion.sm.titlecols}
                        xs={GridProportion.xs.titlecols}
                    >
                        <p>所屬</p>
                    </StyledTitleGridBorder>
                    <StyledGridBorder
                        item={true}
                        sm={GridProportion.sm.contentcols}
                        xs={GridProportion.xs.contentcols}
                    >
                        <p>{editDataItem.belongs}</p>
                    </StyledGridBorder>

                    <StyledTitleGridBorder
                        item={true}
                        sm={GridProportion.sm.titlecols}
                        xs={GridProportion.xs.titlecols}
                    >
                        <p>職位</p>
                    </StyledTitleGridBorder>
                    <StyledGridBorder
                        item={true}
                        sm={GridProportion.sm.contentcols}
                        xs={GridProportion.xs.contentcols}
                    >
                        <p>{editDataItem.position}</p>
                    </StyledGridBorder>

                </StyledGrid>
            </div>
        )
    }
}

export default PageflowDetail
