import React, { Component } from 'react'
// css
const Style = require("./Button_style.module.css");
// shared component
import {
    StyledButtonConfirm,
    StyledButtonCancel,
    StyledAreaFuctionButton
} from 'component/material/StyledButton';
import { UIPatternDisplay } from "component/uipattern/UIPatternDisplay";
// material
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
// kendo

export class ButtonHome extends Component {
    render() {
        return (
            <div>
                <UIPatternDisplay
                    title="確認按鈕"
                    content={
                        <StyledButtonConfirm>
                            確認
                        </StyledButtonConfirm>
                    }
                />

                <UIPatternDisplay
                    title="取消按鈕"
                    content={
                        <StyledButtonCancel>
                            取消
                        </StyledButtonCancel>
                    }
                />

                <UIPatternDisplay
                    title="區塊功能按鈕"
                    content={
                        <React.Fragment>
                            <StyledAreaFuctionButton>
                                新增
                            </StyledAreaFuctionButton>
                            <StyledAreaFuctionButton>
                                編輯
                            </StyledAreaFuctionButton>
                            <StyledAreaFuctionButton>
                                刪除
                            </StyledAreaFuctionButton>
                        </React.Fragment>
                    }
                />

                <UIPatternDisplay
                    title="圖示按鈕"
                    content={
                        <IconButton>
                            <CloseIcon />
                        </IconButton>
                    }
                />

            </div>
        )
    }
}

export default ButtonHome
