import React, { Component } from 'react';
// css
const Style = require("./Popup_style.module.css");
// types
import { PopupStaticContentProps } from './popup';
// shared component
import {
    StyledPopover
} from "component/material/StyledPopup";
import {
    PopupTitle
} from "component/title/Title";
import {
    StyledButtonCancel
} from "component/material/StyledButton";
// material
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';

export const PopupStaticContent: React.FC<PopupStaticContentProps> = (props) => {

    const { open, handleOpenChange } = props;

    return (
        <div>
            <StyledPopover
                open={open}
                style={{
                    background: 'rgba(0, 0, 0, 0.4)',
                    zIndex: 95
                }}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
                transformOrigin={{
                    vertical: 'center',
                    horizontal: 'center',
                }}
            >
                <div className={Style.listitem}>
                    <PopupTitle title="Popup Sample" />
                    <IconButton style={{ position: 'absolute', right: '12px', top: '0px' }}
                        onClick={handleOpenChange}
                    >
                        <CloseIcon />
                    </IconButton>
                </div>
                <div className={Style.popup_content}>
                    <p>
                        昨天，我做了一個夢，一個很久之前的夢，<br />
                        在夢裡，我們還只有13歲。<br />
                        在夢裡，一片被白雪覆蓋的廣闊的田園上，僅僅能從遠方，些許地看到燈光。<br />
                        剛剛積起的雪地，只有我們走過時留下的足跡。
                        </p>
                    <div className={Style.popup_functionarea}>
                        <StyledButtonCancel
                            onClick={handleOpenChange}
                        >
                            關閉
                            </StyledButtonCancel>
                    </div>
                </div>
            </StyledPopover>

        </div >
    )

}

export default PopupStaticContent;
