import React from 'react'
// redux
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import {
    setUserFavoriteData
} from 'reducers/Login/action';
import {
    setUserUsingTaskData
} from 'reducers/Layout/action';
// selector
import {
    getUserUsingTaskData
} from 'reducers/Layout/selector';
import {
    getJwtData,
    getUserid,
    getUserFavoriteData,
    getUserMenuData,
} from 'reducers/Login/selector';
// axios
import Axios from 'axios';
// css
import Style from './header_style.module.css';
// material ui
import StarBorderIcon from '@material-ui/icons/StarBorder'
import ArchiveOutlinedIcon from '@material-ui/icons/ArchiveOutlined'
import StarIcon from '@material-ui/icons/Star';
import IconButton from '@material-ui/core/IconButton';
import Hidden from '@material-ui/core/Hidden';
// kendo ui
import { DropDownList } from '@progress/kendo-react-dropdowns';

import { Trans } from 'react-i18next';
import { NavLink, withRouter } from 'react-router-dom';


class Header extends React.Component {
    constructor(props) {
        super(props);
        this.props = props;
    }

    componentDidMount() {

        this.getUserFavorite();
    }

    getUserFavorite = () => {
        const actions = this.props.actions
        const th = this;

        Axios.get(process.env.REACT_APP_API_SERVER + 'userfavorite', {
            params: {
                userId: this.props.username,
            }, headers: {
                Authorization: this.props.jwtData,
            }
        }).then(function (response) {

            // console.log(response);
            // console.log(response.data.data);
            let data = [];
            let undata = [];
            if (response.data.data !== null) {
                undata = response.data.data;
            }
            undata.map((props) => {
                const o = {};
                o.userId = props[0];
                o.taskId = props[1];
                o.creaeteUser = props[2];
                o.createDatetime = props[3];
                o.taskName = props[4];

                data.push(o);
            })
            actions.setUserFavoriteData(data);
        }).catch(function (error) {
            // console.log(error);
        });
    }

    updateUserFavorite = () => {
        const th = this;
        Axios.post(process.env.REACT_APP_API_SERVER + 'userfavorite', {

            userId: this.props.username,
            taskId: this.props.taskId,
        }, {
                headers: {
                    Authorization: this.props.jwtData,
                }
            }).then(function (response) {

                // console.log(response);
                th.getUserFavorite();
            }).catch(function (error) {
                // console.log(error);
            });
    }

    handleItemChange = () => event => {

        const functionPage = this.props.userMenuData.filter(function f(el) {
            return el.taskId === event.target.value.taskId
        })
        if (functionPage.length > 0) {
            this.props.actions.setUserUsingTaskData(functionPage[0].taskId);
            this.props.history.push(functionPage[0].menuUrl);
        }
    }

    render() {
        const { componentTaskId } = this.props;
        const isFavorite = this.props.data.filter(function f(el) {
            return el.taskId === componentTaskId
        })
        return (
            <div className={Style.outerdiv}>
                <div style={{ fontSize: '22px', color: '#a38f6a', display: 'inline-block' }}>
                    {this.props.title}
                </div>

                <Hidden smDown={true}>

                    <IconButton
                        style={{ paddingTop: '0px' }}
                        onClick={this.updateUserFavorite}
                    >
                        {isFavorite.length > 0 ?
                            <StarIcon /> :
                            <StarBorderIcon />
                        }
                    </IconButton>

                    <a href={this.props.href} download={this.props.download}>
                        <IconButton style={{ paddingTop: '0px', float: 'right' }}>
                            <ArchiveOutlinedIcon />
                        </IconButton>
                    </a>

                    <DropDownList
                        className={Style.dropdown}
                        data={this.props.data}
                        textField="taskName"
                        defaultItem={{
                            taskName:
                                <div className={Style.listitem}>
                                    <StarIcon />
                                    <div className={Style.text}><Trans>我的常用功能</Trans></div>
                                </div>
                        }}
                        onChange={this.handleItemChange()}
                    />

                </Hidden>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    jwtData: getJwtData(state),
    username: getUserid(state),
    userMenuData: getUserMenuData(state),
    taskId: getUserUsingTaskData(state),
    data: getUserFavoriteData(state),
});

const mapDispatchToProps = dispatch => {
    const actions = {
        setUserFavoriteData,
        setUserUsingTaskData,
    }

    return {
        actions: bindActionCreators(actions, dispatch),
        dispatch,
    }
}

export default withRouter(connect(
    mapStateToProps,
    mapDispatchToProps,
)(Header));