// react

// redux
import { combineReducers } from 'redux';
// types
import {
    SET_ALLSYSTEMCODES_DATA,
    SET_PARENTLIST_DATA,
    SET_APPLICATIONCODELIST_DATA,
    SET_DEPARTMENTCODELIST_DATA,
} from './types';
// action
import {
    SetAllSystemCodesData,
    SetParentListData,
    SetApplicationCodeListData,
    SetDepartmentCodeListData,
} from './action';
// other
// import resultTableContentData from 'view/AccessManage/M1005/ResultTableContentData.json'

export interface StoreState {
    Data: any;
}

export function AllSystemCodesData(
    state: StoreState = { Data: [{}], },
    action: SetAllSystemCodesData
): StoreState {
    switch (action.type) {
        case SET_ALLSYSTEMCODES_DATA:
            // state.Data.splice(action.Data.key,1,action.Data[0])
            if (action.Data === null) {
                action.Data = [{}];
            }
            return {
                Data: action.Data,
            };

        default:
            return state;
    }
};

export function ParentListData(
    state: StoreState = { Data: [{}], },
    action: SetParentListData
): StoreState {
    switch (action.type) {
        case SET_PARENTLIST_DATA:
            // state.Data.splice(action.Data.key,1,action.Data[0])
            if (action.Data === null) {
                action.Data = [{}];
            }
            return {
                Data: action.Data,
            };

        default:
            return state;
    }
};

export function ApplicationCodeListData(
    state: StoreState = { Data: [{}], },
    action: SetApplicationCodeListData
): StoreState {
    switch (action.type) {
        case SET_APPLICATIONCODELIST_DATA:
            // state.Data.splice(action.Data.key,1,action.Data[0])
            if (action.Data === null) {
                action.Data = [{}];
            }
            return {
                Data: action.Data,
            };

        default:
            return state;
    }
};

export function DepartmentCodeListData(
    state: StoreState = { Data: [{}], },
    action: SetDepartmentCodeListData
): StoreState {
    switch (action.type) {
        case SET_DEPARTMENTCODELIST_DATA:

            if (action.Data === null) {
                action.Data = [{}];
            }
            return {
                Data: action.Data,
            };

        default:
            return state;
    }
};

const ShareData = combineReducers({
    AllSystemCodesData,
    ParentListData,
    ApplicationCodeListData,
    DepartmentCodeListData,
})

export default ShareData;