import { combineReducers } from 'redux';
// types
import {
    // Verify
    SET_VERIFY_ITEM,
    SET_VERIFY_TRANSATIONID,
    SET_VERIFY_QUESTION,
    // authentication token
    SET_JWT_DATA,
    // Logging
    SET_ISLOGGING,
    // System User Info
    SET_USER_DATA,
    SET_USERMENU_DATA,
    SET_USERCOMPONENTTASK_DATA,
    SET_USERFAVORITE_DATA,
    SET_USERLASTLOGINTIME_DATA,
} from './types'
// action
import {
    // Verify
    VerifyItem,
    // authentication token
    SetJwtData,
    // Logging
    SetIsLogging,
    // System User Info
    SetUserData,
    SetUserMenuData,
    SetUserComponentTaskData,
    SetUserFavoriteData,
    SetUserLastLoginTimeData,
} from './action';


const Verify = (
    state = {
        "item": [],
        "transationId": '',
        "question": '',
    },
    action: VerifyItem
) => {
    switch (action.type) {

        case SET_VERIFY_ITEM:
            return {
                ...state,
                item: action.item,
            }

        case SET_VERIFY_TRANSATIONID:
            return {
                ...state,
                transationId: action.transationId,
            }

        case SET_VERIFY_QUESTION:
            return {
                ...state,
                question: action.question,
            }

        default:
            return state;
    }
}

const JwtData = (state = {
    Data: window.sessionStorage.getItem("jwt") || '',
}, action: SetJwtData) => {
    switch (action.type) {
        case SET_JWT_DATA:
            return {
                Data: action.Data,
            };
        default:
            return state;
    }
};

const Logging = (state = {
    islogging: false,
}, action: SetIsLogging) => {
    switch (action.type) {
        case SET_ISLOGGING:
            return {
                islogging: action.islogging,
            };
        default:
            return state;
    }
};

const UserData = (state = {
    Userid: window.sessionStorage.getItem("userid") || '',
    Username: window.sessionStorage.getItem("username") || '',
}, action: SetUserData) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                Userid: action.Userid,
                Username: action.Username,
            };
        default:
            return state;
    }
};

const UserMenuData = (state = {
    Data: [],
}, action: SetUserMenuData) => {
    switch (action.type) {
        case SET_USERMENU_DATA:
            return {
                Data: action.Data,
            };
        default:
            return state;
    }
};

const UserComponentTaskData = (state = {
    Data: [],
}, action: SetUserComponentTaskData) => {
    switch (action.type) {
        case SET_USERCOMPONENTTASK_DATA:
            return {
                Data: action.Data,
            };
        default:
            return state;
    }
};

const UserFavoriteData = (state = {
    Data: [],
}, action: SetUserFavoriteData) => {
    switch (action.type) {
        case SET_USERFAVORITE_DATA:
            return {
                Data: action.Data,
            };
        default:
            return state;
    }
};

const UserLastLoginTimeData = (state = {
    Data: "",
}, action: SetUserLastLoginTimeData) => {
    switch (action.type) {
        case SET_USERLASTLOGINTIME_DATA:
            return {
                Data: action.Data,
            };
        default:
            return state;
    }
};

const Login = combineReducers({
    Verify,
    Logging,
    JwtData,
    UserData,
    UserMenuData,
    UserComponentTaskData,
    UserFavoriteData,
    UserLastLoginTimeData
})

export default Login;