import React, { Component } from 'react'

const Style = require('./Loading_style.module.css')

const loadingLogo = require('asset/inc/img/global/preload_logo.png');
const loadingImage = require('asset/inc/img/global/sprites.png');

import { UIPatternDisplay } from "component/uipattern/UIPatternDisplay";

import Button from '@material-ui/core/Button';

const Loading = (props) => {

    const { start, handleEndClick } = props;

    const loadingBackgroundRef = React.createRef<HTMLDivElement>();
    const loadingImageRef = React.createRef<HTMLDivElement>();

    const cSpeed = 5;
    const cWidth = 50;
    const cHeight = 51;
    const cTotalFrames = 20;
    const cFrameWidth = 50;
    // const cImageSrc = '../../asset/inc/img/global/sprites.png';

    let cImageTimeout: number | undefined;
    let cIndex = 0;
    let cXpos = 0;
    let cPreloaderTimeout: number | undefined;
    let SECONDS_BETWEEN_FRAMES = 0;

    const startAnimation = () => {

        if (loadingImageRef.current) {

            loadingImageRef.current.style.backgroundImage = 'url(' + loadingImage + ')';
            loadingImageRef.current.style.width = cWidth + 'px';
            loadingImageRef.current.style.height = cHeight + 'px';
        }
        const fps = Math.round(100 / cSpeed);
        SECONDS_BETWEEN_FRAMES = 1 / fps;

        cPreloaderTimeout = setTimeout(continueAnimation, SECONDS_BETWEEN_FRAMES / 1000);
    }

    const continueAnimation: TimerHandler = () => {

        cXpos += cFrameWidth;
        // increase the index so we know which frame of our animation we are currently on
        cIndex += 1;

        // if our cIndex is higher than our total number of frames, we're at the end and should restart
        if (cIndex >= cTotalFrames) {
            cXpos = 0;
            cIndex = 0;
        }

        if (loadingImageRef.current) {
            loadingImageRef.current.style.backgroundPosition = (-cXpos) + 'px 0';
        }

        cPreloaderTimeout = setTimeout(continueAnimation, SECONDS_BETWEEN_FRAMES * 1000);
        if (!start) { stopAnimation(); }
    }

    const stopAnimation = () => { // stops animation

        clearTimeout(cPreloaderTimeout);
        cPreloaderTimeout = undefined;
    }

    // Pre-loads the sprites image
    const imageLoader = (s, fun: TimerHandler) => {
        clearTimeout(cImageTimeout);
        cImageTimeout = 0;
        const genImage = new Image();
        genImage.onload = function () {
            cImageTimeout = setTimeout(fun, 0)
        };
        genImage.onerror = () => {
            return (
                alert('Could not load the image')
            )
        }
        // genImage.onerror = new Function('alert(\'Could not load the image\')');
        genImage.src = s;
    }

    new imageLoader(loadingImage, startAnimation);

    const fade = start ? Style.fadeIn : Style.fadeOut;

    return (
        <div id='loadingBackground' ref={loadingBackgroundRef} className={Style.loading_background + ' ' + fade} onClick={handleEndClick}>
            <div className={Style.loading}>
                <img src={loadingLogo} alt=''></img>
                <div id='loadingImage' ref={loadingImageRef} className={Style.loading_image}></div>
            </div>
        </div>
    )
}

export class LoadingHome extends Component {

    readonly state = {
        loadingShow: false,
        start: false,
    }

    handleStartClick = () => { this.setState({ start: !this.state.start }); }
    handleEndClick = () => { this.setState({ start: !this.state.start }); }

    render() {
        const { start } = this.state;

        return (
            <div>
                <UIPatternDisplay
                    title="載入圖案"
                    content={
                        <Button onClick={this.handleStartClick}
                        >開始loading</Button>
                    }
                />
                <Loading start={start} handleEndClick={this.handleEndClick} />
            </div>
        )
    }
}

export default LoadingHome
