import twmsMenuRoutes from '../../routes/twmsMenuRoutes';
import { combineReducers } from 'redux';
// types
import {
    SET_SIDEBARLIST_ITEM,
    SET_SIDEBARLIST_OPEN,
    SET_SIDEBARLIST_MOBILEMENUEXPANDED,
    SET_USERMENULAYOUT_DATA,
    SET_USERUSINGTASKID_DATA,
    SET_DRAWER_OPEN
} from './types'
// action
import {
    Sidebarlist,
    SetUserMenuLayoutData,
    SetUserUsingTaskData,
    SetDrawerOpen,
} from './action';

export interface SidebarListItemState {
    item: object[];
}

const SidebarListItem = (
    state: SidebarListItemState = {
        item: twmsMenuRoutes[0].children,
    },
    action: Sidebarlist) => {
    switch (action.type) {
        case SET_SIDEBARLIST_ITEM:
            return {
                item: action.item,
            };
        default:
            return state;
    }
};

export interface SidebarListOpenState {
    open: boolean;
}

const SidebarListOpen = (
    state: SidebarListOpenState = {
        open: true,
    }, action: Sidebarlist) => {
    switch (action.type) {
        case SET_SIDEBARLIST_OPEN:
            return {
                open: !action.open,
            };
        default:
            return state;
    }
};

export interface MobileMenuExpandedState {
    expanded: boolean;
}

const MobileMenuExpanded = (
    state: MobileMenuExpandedState = {
        expanded: false,
    }, action: Sidebarlist) => {
    switch (action.type) {
        case SET_SIDEBARLIST_MOBILEMENUEXPANDED:
            return {
                expanded: action.expanded,
            };
        default:
            return state;
    }
};

export interface UserMenuLayoutDataState {
    Data: any;
}

const UserMenuLayoutData = (
    state: UserMenuLayoutDataState = {
        Data: [{}],
    }, action: SetUserMenuLayoutData) => {
    switch (action.type) {
        case SET_USERMENULAYOUT_DATA:
            return {
                Data: action.Data,
            };
        default:
            return state;
    }
};

export interface UserUsingTaskDataState {
    Data: any;
}

const UserUsingTaskData = (
    state: UserUsingTaskDataState = {
        Data: window.sessionStorage.getItem("userUsingTask") || "",
    }, action: SetUserUsingTaskData) => {
    switch (action.type) {
        case SET_USERUSINGTASKID_DATA:
            return {
                Data: action.Data,
            };
        default:
            return state;
    }
};

export interface DrawerOpenState {
    open: boolean;
}

const DrawerOpen = (
    state: DrawerOpenState = {
        open: false,
    }, action: SetDrawerOpen) => {
    switch (action.type) {
        case SET_DRAWER_OPEN:
            return {
                open: !action.open,
            };
        default:
            return state;
    }
};

const Layout = combineReducers({
    SidebarListItem,
    SidebarListOpen,
    MobileMenuExpanded,
    UserMenuLayoutData,
    UserUsingTaskData,
    DrawerOpen,
})

export default Layout;