import React from 'react';
// css
const Style = require("./Alert_style.module.css");
// shared component
import { AlertBlock } from 'component/alert/Alert';
import { UIPatternDisplay } from "component/uipattern/UIPatternDisplay";
// material
import Button from '@material-ui/core/Button';
import Snackbar from '@material-ui/core/Snackbar';

class AlertHome extends React.Component {

    readonly state = {
        snackbarOpen: false,
        alertOpen: true,
    }

    /**
     * Usage: 顯示提示元件，設定提示訊息
     * Param: 
     *      msg: 回應使用者操作提示訊息
     * As Callback: <BAS011001MPopupEdit />, deleteSingleData, deleteMultiData
     */
    public handleSnackbarOpen = () => {
        this.setState({
            snackbarOpen: true,
        })
    }

    /**
     * Usage: 關閉提示元件，清除提示訊息
     * Param: 
     *      event: 
     *      reason: 提示元件onClose 觸發的原因
     * As Callback: <Snackbar />
     */
    public handleSnackbarClose = () => {
        this.setState({
            snackbarOpen: false,
        })
    }

    /**
     * 
     */
    public handleAlertChange = () => {
        this.setState({
            alertOpen: !this.state.alertOpen,
        })
    }

    render() {

        const { snackbarOpen, alertOpen } = this.state;

        return (
            <div>
                <UIPatternDisplay
                    title="操作成功提示訊息"
                    content={
                        <React.Fragment>
                            <Button
                                onClick={this.handleSnackbarOpen}
                            >操作</Button>
                            <Snackbar
                                anchorOrigin={{
                                    vertical: 'bottom',
                                    horizontal: 'right',
                                }}

                                autoHideDuration={3000}
                                open={snackbarOpen}
                                onClose={this.handleSnackbarClose}
                                message={<span>操作成功提示訊息</span>}
                            />
                        </React.Fragment>
                    }
                />

                <UIPatternDisplay
                    title="操作失敗提示訊息"
                    content={
                        <React.Fragment>
                            <Button
                                onClick={this.handleAlertChange}
                            >操作</Button>
                            <div hidden={alertOpen}>
                                <AlertBlock
                                    alertMessage="操作失敗提示訊息"
                                />
                            </div>
                        </React.Fragment>
                    }
                />
            </div>
        )
    }
}

export default AlertHome;