import {
    SET_ALLSYSTEMCODES_DATA,
    SET_PARENTLIST_DATA,
    SET_APPLICATIONCODELIST_DATA,
    SET_DEPARTMENTCODELIST_DATA,
} from './types';

export interface SetAllSystemCodesData {
    type: SET_ALLSYSTEMCODES_DATA;
    Data: any;
}
export function setAllSystemCodesData(Data: any): SetAllSystemCodesData {
    return {
        type: SET_ALLSYSTEMCODES_DATA,
        Data,
    }
};


