import React, { Component } from 'react';

import { MultiInputState, GridData } from './multiinput';

import { UIPatternDisplay } from "component/uipattern/UIPatternDisplay";

import MultiInputGrid from './MultiInput_Grid';
import MultiInputErrorPopup from './MultiInput_ErrorPopup';

export class MultiInput extends Component<any, MultiInputState> {

    readonly state: MultiInputState = {
        data: [
            { ProductID: 1, ProductName: 'A', FirstOrderedOn: new Date(), UnitsInStock: 1, inEdit: false },
            { ProductID: 2, ProductName: 'B', FirstOrderedOn: new Date(), UnitsInStock: 1, inEdit: false },
            { ProductID: 3, ProductName: 'C', FirstOrderedOn: new Date(), UnitsInStock: 1, inEdit: false },
        ],
        editId: undefined, // unuse
        errorOpen: false,
        errorMsg: [""],
    }

    handleRowClick = (e) => {
        //
        // this.setState({ editId: e.dataItem.id });
    }

    itemChange = (event) => {
        const value = event.value;
        const name = event.field;
        if (!name) {
            return;
        }
        const updatedData = this.state.data.slice();
        const item = updatedData[updatedData.indexOf(event.dataItem)];
        item[name] = value;
        this.setState({
            data: updatedData
        });
    }

    handleCreateClick = () => {
        const { data } = this.state;
        const nextId = data.length + 1;
        const dataItem: GridData[] = [{
            ProductID: nextId,
            ProductName: undefined,
            FirstOrderedOn: undefined,
            UnitsInStock: undefined,
            inEdit: true
        }];
        const newData = data.concat(dataItem);
        this.setState({ data: newData, editId: nextId });
    }

    handleEditClick = (dataItem) => (event) => {
        //
        const index = dataItem.ProductID - 1;
        const data = this.state.data.slice();
        data[index].inEdit = true;

        this.setState({ data: data });
    }

    handelCancelClick = (dataItem) => (event) => {
        //
        const index = dataItem.ProductID - 1;
        const data = this.state.data.slice();
        data[index].inEdit = false;

        this.setState({ data: data });
    }

    handleErrorPopupClick = () => {
        this.setState({ errorOpen: false });
    }

    handleSubmitClick = () => {
        //
        const data = this.state.data;
        const inEditData = data.filter(el => el.inEdit === true);
        const errorMsg: string[] = [];
        inEditData.map(prop => {
            //
            const { ProductID, ProductName, FirstOrderedOn, UnitsInStock } = prop;
            if (ProductName) {
                // errorMsg = errorMsg + ProductName.toString() + "查無此貨物" + "{<br />}";
                errorMsg.push("訂單項目id: " + ProductID.toString() + "錯誤，" + "查無此貨物名：" + ProductName.toString());
            }
            if (UnitsInStock) {
                // errorMsg = errorMsg + UnitsInStock.toString() + "貨物庫存不足" + "{<br />}";
                errorMsg.push("訂單項目id: " + ProductID.toString() + "錯誤，" + "貨物庫存不足" + UnitsInStock.toString());
            }
        });

        this.setState({ errorOpen: true, errorMsg: errorMsg });
    }

    render() {

        const { data, errorOpen, errorMsg } = this.state;
        const multiInputDisplay = <div>
            <MultiInputGrid
                data={data}
                handleRowClick={this.handleRowClick}
                itemChange={this.itemChange}
                handleCreateClick={this.handleCreateClick}
                handleEditClick={this.handleEditClick}
                handelCancelClick={this.handelCancelClick}
                handleSubmitClick={this.handleSubmitClick}
            />
            <MultiInputErrorPopup
                errorOpen={errorOpen}
                errorMsg={errorMsg}
                handleErrorPopupClick={this.handleErrorPopupClick}
            />
        </div>

        return (
            <UIPatternDisplay
                title="多筆輸入錯誤訊息顯示"
                content={
                    multiInputDisplay
                }
            />
        )
    }
}

export default MultiInput;
