import React from 'react';

import Button from '@material-ui/core/Button';

import { Grid, GridColumn as Column, GridToolbar } from '@progress/kendo-react-grid';


export const MultiInputGrid: React.FC<any> = (props) => {

    const { data,
        handleRowClick, itemChange,
        handleCreateClick, handleEditClick, handelCancelClick, handleSubmitClick
    } = props;

    return (
        <Grid
            data={data}
            editField="inEdit"
            scrollable='scrollable'
            resizable={true}
            // onRowClick={handleRowClick}
            onItemChange={itemChange}
        >
            <GridToolbar>
                <Button
                    title="Add new"
                    onClick={handleCreateClick}
                >Add new
                </Button>

                {data.filter(p => p.inEdit).length > 0 && (
                    <Button
                        title="Cancel current changes"
                        onClick={handleSubmitClick}
                    >Submit
                    </Button>
                )}
            </GridToolbar>
            <Column field="ProductID" title="Id" width="50px" editable={false} />
            <Column field="ProductName" title="Product Name" />
            <Column field="FirstOrderedOn" title="First Ordered" editor="date" format="{0:d}" />
            <Column field="UnitsInStock" title="Units" width="150px" editor="numeric" />
            <Column width="180px"
                cell={(prop) => {

                    const dataItem = prop.dataItem;
                    const buttonValue = dataItem.inEdit ? '取消' : '編輯'
                    const buttonClick = dataItem.inEdit ? handelCancelClick(dataItem) : handleEditClick(dataItem)

                    return (
                        <td>
                            <Button
                                onClick={buttonClick}
                            >
                                {buttonValue}
                            </Button>
                        </td>
                    )
                }}
            />
            {/* <Column field="Discontinued" title="Discontinued" editor="boolean" /> */}
            {/* <Column cell={this.CommandCell} width="180px" /> */}
        </Grid>
    )
}
export default MultiInputGrid;