import React, { Component } from 'react';
// css
const Style = require("./Form_style.module.css");
// shared component
import { UIPatternDisplay } from "component/uipattern/UIPatternDisplay";
// presentional component
import SingleDataInput from "./Form_singleDataInput";
import DataDisplayGrids from "./Form_dataDisplayGrids";

export class FormHome extends Component {
    render() {
        return (
            <div>
                <UIPatternDisplay
                    title="單筆輸入表格"
                    content={
                        <SingleDataInput />
                    }
                />

                <UIPatternDisplay
                    title="資料顯示表格"
                    content={
                        <DataDisplayGrids />
                    }
                />
            </div>
        )
    }
}

export default FormHome
