// react

// redux
import { combineReducers } from 'redux';
// types
import { 
    SET_SYSTEMCODESTABLE_DATA,
 } from './types';
// action
import {SetSystemCodesTableData} from './action';
// other
// import resultTableContentData from 'view/AccessManage/M1005/ResultTableContentData.json'

export interface BAS011001MStoreState {
    Data: any;
}

export function SystemCodesTableData(
        state: BAS011001MStoreState = { Data: [], }, 
        action: SetSystemCodesTableData
    ): BAS011001MStoreState {
    switch (action.type) {
        case SET_SYSTEMCODESTABLE_DATA:
            // state.Data.splice(action.Data.key,1,action.Data[0])
            if(action.Data===null){
                action.Data=[];
            }
            return {
                Data: action.Data,
            };
        
        default:
            return state;
    }
};

const BAS011001M = combineReducers({
    SystemCodesTableData,
})

export default BAS011001M;