import React, { Component } from 'react';
// css
const Style = require("./Popup_style.module.css");
// shared component
import {
    StyledPopover
} from "component/material/StyledPopup";
import {
    PopupTitle
} from "component/title/Title";
import {
    StyledButtonCancel
} from "component/material/StyledButton";
import { UIPatternDisplay } from "component/uipattern/UIPatternDisplay";
// material
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
// presentional component
import PopupStaticContent from './Popup_staticContent';
import PopupKendoComponent from './Popup_kendoComponent';

export class PopupHome extends Component {

    readonly state = {
        open: false,
        openKendo: false,
    }

    public handleOpenChange = () => {
        this.setState({ open: !this.state.open });
    }

    public handleOpenKendoChange = () => {
        this.setState({ openKendo: !this.state.openKendo });
    }

    render() {

        const { open, openKendo } = this.state;

        const popupStaticContentShow = open && <PopupStaticContent
            open={open}
            handleOpenChange={this.handleOpenChange}
        />

        const popupKendoComponent = openKendo && <PopupKendoComponent
            open={openKendo}
            handleOpenChange={this.handleOpenKendoChange}
        />

        return (
            <div>
                <UIPatternDisplay
                    title="範例Popup"
                    content={
                        <Button
                            onClick={this.handleOpenChange}
                        >Click Me!</Button>
                    }
                />
                {popupStaticContentShow}

                <UIPatternDisplay
                    title="範例Popup with Kendo DatePicker and DropDownList"
                    content={
                        <Button
                            onClick={this.handleOpenKendoChange}
                        >Click Me!</Button>
                    }
                />
                {popupKendoComponent}
            </div>
        )
    }
}

export default PopupHome
