import {
    SET_SIDEBARLIST_ITEM,
    SET_SIDEBARLIST_OPEN,
    SET_SIDEBARLIST_MOBILEMENUEXPANDED,
    SET_USERMENULAYOUT_DATA,
    SET_USERUSINGTASKID_DATA,
    SET_DRAWER_OPEN,
} from './types'

export interface SetSidebarlistItem {
    type: SET_SIDEBARLIST_ITEM;
    item: object[];
}

export interface SetSidebarlistOpen {
    type: SET_SIDEBARLIST_OPEN;
    open: boolean;
}

export interface SetSidebarlistMobilemenuexpanded {
    type: SET_SIDEBARLIST_MOBILEMENUEXPANDED;
    expanded: boolean;
}

export type Sidebarlist =
    SetSidebarlistItem |
    SetSidebarlistOpen |
    SetSidebarlistMobilemenuexpanded;

export const setSidebarlistItem: (item: object[]) => SetSidebarlistItem =
    (item) => ({
        type: SET_SIDEBARLIST_ITEM,
        item
    });
export const setSidebarlistOpen: (item: boolean) => SetSidebarlistOpen =
    (open) => ({
        type: SET_SIDEBARLIST_OPEN,
        open
    });
export const setSidebarlistMobilemenuexpanded: (item: boolean) => SetSidebarlistMobilemenuexpanded =
    (expanded) => ({
        type: SET_SIDEBARLIST_MOBILEMENUEXPANDED,
        expanded,
    });


export interface SetUserMenuLayoutData {
    type: SET_USERMENULAYOUT_DATA;
    Data: any;
}
export const setUserMenuLayoutData: (Data: any) => SetUserMenuLayoutData =
(Data) => ({
    type: SET_USERMENULAYOUT_DATA,
    Data,
});

export interface SetUserUsingTaskData {
    type: SET_USERUSINGTASKID_DATA;
    Data: any;
}
export const setUserUsingTaskData: (Data: any) => SetUserUsingTaskData =
(Data) => ({
    type: SET_USERUSINGTASKID_DATA,
    Data,
});

export interface SetDrawerOpen {
    type: SET_DRAWER_OPEN;
    open: boolean;
}
export const setDrawerOpen: (open: boolean) => SetDrawerOpen =
(open) => ({
    type: SET_DRAWER_OPEN,
    open,
});