import React, { Component } from 'react';
// css
const Style = require("./Tab_style.module.css");
// shared component
import { UIPatternDisplay } from "component/uipattern/UIPatternDisplay";
// material
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
// sample content
const TabContent = require("./Tab_sample_content.json");

const TabContainer = (props) => {

    const { msg } = props;

    return (
        <div className={Style.tabcontainer}>{msg}</div>
    )
};

export const TabBar = () => {

    const [tabValue, setTabValue] = React.useState(0);

    const handleTabValueChange = (event: React.ChangeEvent<{}>, newValue) => {
        setTabValue(newValue);
    }

    return (
        <div>
            <Tabs value={tabValue} onChange={handleTabValueChange} >
                <Tab label="魏" />
                <Tab label="蜀" />
                <Tab label="吳" />
            </Tabs>
            {tabValue === 0 && <TabContainer msg={TabContent.tab1} />}
            {tabValue === 1 && <TabContainer msg={TabContent.tab2} />}
            {tabValue === 2 && <TabContainer msg={TabContent.tab3} />}
        </div>
    )
}

export class TabHome extends Component {

    render() {
        return (
            <div>
                <UIPatternDisplay
                    title="範例頁籤"
                    content={
                        <TabBar />
                    }
                />
            </div>
        )
    }
}

export default TabHome
