import {
    INSERT_GRID_DATA,
    UPDATE_GRID_DATA,
    DELETE_GRID_DATA,
    DELETE_MULTIGRID_DATA,
} from "./types";

interface TDataItem {
    id: number,
    name: string,
    belongs: string,
    position: string,
}

export interface IInsertGridData {
    type: INSERT_GRID_DATA,
    dataItem: TDataItem
}

export const insertGridData: (dataItem: TDataItem) => IInsertGridData =
    (dataItem) => ({
        type: INSERT_GRID_DATA,
        dataItem
    });

export interface IUpdateGridData {
    type: UPDATE_GRID_DATA,
    dataItem: TDataItem
}

export const updateGridData: (dataItem: TDataItem) => IUpdateGridData =
    (dataItem) => ({
        type: UPDATE_GRID_DATA,
        dataItem
    });

export interface IDeleteGridData {
    type: DELETE_GRID_DATA,
    dataItem: TDataItem
}

export const deleteGridData: (dataItem: TDataItem) => IDeleteGridData =
    (dataItem) => ({
        type: DELETE_GRID_DATA,
        dataItem
    });

export interface IDeleteMultiGridData {
    type: DELETE_MULTIGRID_DATA,
    dataItem: TDataItem[]
}

export const deleteMultiGridData: (dataItem: TDataItem[]) => IDeleteMultiGridData =
    (dataItem) => ({
        type: DELETE_MULTIGRID_DATA,
        dataItem
    });

export type GridData =
    IInsertGridData |
    IUpdateGridData |
    IDeleteGridData |
    IDeleteMultiGridData;
