import React, { Component } from 'react';
// redux
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
// selector
// css
const Style = require("./Pageflow_style.module.css");
// type
import {
    IPageflowHomeProps
} from "./IPageflow_home";
// shared component
import { UIPatternDisplay } from "component/uipattern/UIPatternDisplay";
// 
import PageflowGrids from "./Pageflow_grids";
import PageflowForm from "./Pageflow_form";
import PageflowDetail from "./Pageflow_detail";

export class PageflowHome extends Component<IPageflowHomeProps> {

    readonly state = {
        nextId: 0,
        pageShow: "",
        detailOpen: false,
        editDataItem: {},
    }

    handleCreateClick = (maxId: number) => event => {

        this.setState({
            nextId: maxId + 1,
            pageShow: "create",
            editDataItem: {},
        })
    }

    handleEditClick = (dataItem) => event => {

        this.setState({
            pageShow: "edit",
            editDataItem: dataItem.dataItem,
        })
    }

    handleHomeChange = () => {

        this.setState({
            pageShow: "",
            editDataItem: {},
        })
    }

    handleHomeClick = () => event => {

        this.setState({
            pageShow: "",
            editDataItem: {},
        })
    }

    handleDetailOpen = (dataItem) => event => {
        this.setState({
            pageShow: "detail",
            editDataItem: dataItem.dataItem,
        })
    }

    render() {

        const { nextId, pageShow, detailOpen, editDataItem } = this.state;

        let display =
            <PageflowGrids
                handleCreateClick={this.handleCreateClick}
                handleEditClick={this.handleEditClick}
                handleDetailOpen={this.handleDetailOpen}
            />;
        if (pageShow === "create") {
            display =
                <PageflowForm
                    nextId={nextId}
                    pageShow={pageShow}
                    editDataItem={editDataItem}
                    handleHomeClick={this.handleHomeClick}
                    handleHomeChange={this.handleHomeChange}
                />;
        } else if (pageShow === "edit") {
            display =
                <PageflowForm
                    pageShow={pageShow}
                    editDataItem={editDataItem}
                    handleHomeClick={this.handleHomeClick}
                    handleHomeChange={this.handleHomeChange}
                />;
        } else if (pageShow === "detail") {
            display =
                <PageflowDetail
                    editDataItem={editDataItem}
                    handleHomeClick={this.handleHomeClick}
                />;
        }

        return (
            <div>
                <UIPatternDisplay
                    title="頁面流範例"
                    content={
                        display
                    }
                />
            </div>
        )
    }
}

const mapStateToProps = state => ({
});

const mapDispatchToProps = (dispatch: Dispatch) => {
    const actions = {
    }

    return {
        actions: bindActionCreators(actions, dispatch),
        dispatch,
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(PageflowHome);
