import React, { Component } from 'react'
// css
const Style = require("./Form_style.module.css");
// shared component
import { Title } from 'component/title/Title.jsx';
import {
    StyledAreaFuctionButton
} from 'component/material/StyledButton';
// material component
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
// kendo component
import { Grid, GridColumn, GridToolbar, GridCellProps, GridPageChangeEvent, GridRowClickEvent } from '@progress/kendo-react-grid';
// other

const dataExample = [];

export class DataDisplayGrids extends Component {

    readonly state = {
        skip: 0,
        take: 10,
        anchorEl: null,
    }

    /**
     * Usage: grid顯示區間改變
     * Param: event
     * As Callback: 
     */
    public handlePageChange = (event: GridPageChangeEvent) => {
        this.setState({
            skip: event.page.skip,
            take: event.page.take,
        });
    }

    /**
     * Usage: 設定menu元件render定位點
     * Param: event
     * As Callback: 
     */
    handleClick = () => (event: React.MouseEvent<HTMLElement, MouseEvent>) => {
        this.setState({ anchorEl: event.currentTarget });
    }

    /**
     * Usage: 清除menu元件render定位點
     * Param: event
     * As Callback: 
     */
    handleClose = () => {
        this.setState({ anchorEl: null });
    }

    render() {

        const { skip, take, anchorEl } = this.state;
        const gridRangeData = dataExample.slice(skip, take + skip);

        return (
            <div>
                <Grid
                    style={{ fontWeight: 100 }}
                    data={gridRangeData}
                    skip={skip}
                    take={take}
                    total={dataExample.length}
                    pageable={{
                        buttonCount: 5,
                        info: true,
                        type: 'numeric',
                        pageSizes: true,
                        previousNext: true,
                    }}
                    onPageChange={this.handlePageChange}
                    sortable={true}
                    resizable={true}
                    pageSize={skip}
                    scrollable='scrollable'
                    headerCellRender={(defaultRendering, props) => (
                        <div
                            style={{ fontSize: "1rem" }}
                        >{defaultRendering}</div>
                    )}
                >
                    <GridToolbar>
                        <div className={Style.listitem}>
                            <Title title="範例清單" />
                            <StyledAreaFuctionButton
                            >新增</StyledAreaFuctionButton>
                            <StyledAreaFuctionButton
                            >多筆刪除</StyledAreaFuctionButton>
                            <StyledAreaFuctionButton
                                aria-owns={anchorEl ? 'render-props-popover' : undefined}
                                aria-haspopup="true"
                                onClick={this.handleClick()}
                            >下載</StyledAreaFuctionButton>
                            <Menu
                                id="render-props-popover"
                                anchorEl={anchorEl}
                                open={Boolean(anchorEl)}
                                onClose={this.handleClose}
                                disableAutoFocus={true}
                                disableAutoFocusItem={true}
                                anchorOrigin={{
                                    vertical: 'bottom',
                                    horizontal: 'center',
                                }}
                                transformOrigin={{
                                    vertical: 'top',
                                    horizontal: 'center',
                                }}
                                getContentAnchorEl={null}
                            >
                                <MenuItem
                                    onClick={this.handleClose}
                                >Excel</MenuItem>
                                <MenuItem
                                    onClick={this.handleClose}
                                >PDF</MenuItem>
                                <MenuItem
                                    onClick={this.handleClose}
                                >CSV</MenuItem>
                            </Menu>
                        </div>
                    </GridToolbar>
                    <GridColumn
                        width='64px'
                        cell={() => {
                            return (
                                <td>
                                    <input type='checkbox' />
                                </td>
                            )
                        }}
                        headerCell={() => {

                            return (
                                <div style={{ padding: '0', borderBottom: '0' }}>
                                    <input type='checkbox' />
                                </div>
                            )
                        }}
                    />
                    <GridColumn width="250px" title="操作功能"
                        cell={() => {
                            return (
                                <td>
                                    <StyledAreaFuctionButton
                                    >明細</StyledAreaFuctionButton>
                                    <StyledAreaFuctionButton
                                    >編輯</StyledAreaFuctionButton>
                                    <StyledAreaFuctionButton
                                    >刪除</StyledAreaFuctionButton>
                                </td>
                            )
                        }}
                    />
                    <GridColumn width='100px' field="id" title="代碼" />
                    <GridColumn width='160px' field="name" title="名字" />
                    <GridColumn width="100px" field="belongs" title="所屬" />
                    <GridColumn width="160px" field="position" title="職位" />
                    <GridColumn field="last" title=" " />

                </Grid>
            </div>
        )
    }
}

export default DataDisplayGrids
