import React from 'react';

const Style = require('./MultiInput_style.module.css');

import Popover from '@material-ui/core/Popover';
import Button from '@material-ui/core/Button';

import { PopupTitle } from 'component/title/Title';

export const MultiInputErrorPopup: React.FC<any> = (props) => {

    const { errorOpen, errorMsg, handleErrorPopupClick } = props;

    return (
        <Popover
            open={errorOpen}
            style={{
                background: "rgba(0, 0, 0, 0.4)",
                zIndex: 100
            }}
            anchorOrigin={{
                vertical: "bottom",
                horizontal: "center"
            }}
            transformOrigin={{
                vertical: "center",
                horizontal: "center"
            }}
        >
            <div className={Style.popup}>
                <PopupTitle title="錯誤訊息" />
                <div className={Style.popup_content}>
                    {errorMsg.map(msg => <>
                        {msg}<br />
                    </>)}
                </div>
                <div className={Style.popup_functionarea}>
                    <Button
                        onClick={handleErrorPopupClick}
                    >關閉</Button>
                </div>
            </div>
        </Popover>
    )
}

export default MultiInputErrorPopup;
