import { combineReducers } from 'redux';

import {
    INSERT_GRID_DATA,
    UPDATE_GRID_DATA,
    DELETE_GRID_DATA,
    DELETE_MULTIGRID_DATA,
} from "./types";
import {
    GridData
} from "./action";

const GridsData = (
    state = {
        dataItem: [
            { id: 1, name: "劉備", belongs: "蜀", position: "君主", },
            { id: 2, name: "關羽", belongs: "蜀", position: "武將", },
            { id: 3, name: "張飛", belongs: "蜀", position: "武將", },
            { id: 4, name: "諸葛亮", belongs: "蜀", position: "軍師", },
            { id: 5, name: "曹操", belongs: "魏", position: "君主", },
            { id: 6, name: "夏侯惇", belongs: "魏", position: "武將", },
            { id: 7, name: "夏侯淵", belongs: "魏", position: "武將", },
            { id: 8, name: "郭嘉", belongs: "魏", position: "軍師", },
            { id: 9, name: "孫權", belongs: "吳", position: "君主", },
            { id: 10, name: "太史慈", belongs: "吳", position: "武將", },
            { id: 11, name: "周泰", belongs: "吳", position: "武將", },
            { id: 12, name: "周瑜", belongs: "吳", position: "軍師", },
        ],
    },
    action: GridData
) => {
    switch (action.type) {

        case INSERT_GRID_DATA:

            const insertedData = state.dataItem.slice();
            insertedData.push(action.dataItem);

            return {
                dataItem: insertedData,
            }

        case UPDATE_GRID_DATA:

            const updatedData = state.dataItem.slice().filter(function f(el) {
                if (el.id === action.dataItem.id) {
                    el.name = action.dataItem.name;
                    el.belongs = action.dataItem.belongs;
                    el.position = action.dataItem.position;
                }

                return true;
            })

            return {
                dataItem: updatedData,
            }

        case DELETE_GRID_DATA:

            const indexNum = state.dataItem.slice().map(item => Object.values(item)[0]).indexOf(action.dataItem.id);
            const deletedData = state.dataItem.slice();
            deletedData.splice(indexNum, 1);

            return {
                dataItem: deletedData,
            }

        case DELETE_MULTIGRID_DATA:

            const multiDeletedData = state.dataItem.slice();
            action.dataItem.map(ele => {
                const indexNumber = multiDeletedData.map(item => Object.values(item)[0]).indexOf(ele.id);
                multiDeletedData.splice(indexNumber, 1);
            })

            return {
                dataItem: multiDeletedData,
            }

        default:
            return state;
    }
}

const Grids = combineReducers({
    GridsData,
})

export default Grids;