import React, { Component } from 'react';

const Style = require('./Title_style.module.css');

import { Title } from 'component/title/Title';
import Header from 'component/header/Header';
import { UIPatternDisplay } from "component/uipattern/UIPatternDisplay";

export class TitleHome extends Component {
    render() {
        return (
            <div>
                <UIPatternDisplay
                    title="功能主標題"
                    content={
                        <Header
                            title="Sample Code 範例標題"
                            componentTaskId="Title"
                            href={null}
                            download="SampleCodeTitle.docx"
                        />
                    }
                />

                <UIPatternDisplay
                    title="功能區塊標題"
                    content={
                        <Title title="範例標題" />
                    }
                />
            </div>
        )
    }
}

export default TitleHome
