import React, { Component } from 'react';
// redux
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import {
    insertGridData,
    updateGridData
} from "reducers/UIPattern/action";
// css
const Style = require("./Pageflow_style.module.css");
// type
import {
    IPageflowFormProps
} from "./IPageflow_form";
// shared component
import {
    StyledGrid,
    StyledTitleGridBorder,
    StyledGridBorder,
    GridProportion,
} from 'component/material/StyledGrid';
import Taskbar from 'component/taskbar/Taskbar';
import { Title } from 'component/title/Title.jsx';
// material

// kendo component
import { Input } from '@progress/kendo-react-inputs';
import { DropDownList, DropDownListChangeEvent } from '@progress/kendo-react-dropdowns';


const InputForm = (props) => {

    const { form, id, name, belongs, position,
        handleNameChange, handleBelongsChange, handlePositionChange } = props;

    return (
        <form ref={form} >
            <StyledGrid
                container={true}
            >
                <StyledTitleGridBorder
                    item={true}
                    sm={GridProportion.sm.titlecols}
                    xs={GridProportion.xs.titlecols}
                >
                    <p>代碼</p>
                </StyledTitleGridBorder>
                <StyledGridBorder
                    item={true}
                    sm={GridProportion.sm.contentcols}
                    xs={GridProportion.xs.contentcols}
                >
                    <Input className={Style.input}
                        type='text'
                        value={id}
                        disabled={true}
                    />
                </StyledGridBorder>

                <StyledTitleGridBorder
                    item={true}
                    sm={GridProportion.sm.titlecols}
                    xs={GridProportion.xs.titlecols}
                >
                    <p>
                        <span style={{ color: "red" }}>*</span>
                        名字
                    </p>
                </StyledTitleGridBorder>
                <StyledGridBorder
                    item={true}
                    sm={GridProportion.sm.contentcols}
                    xs={GridProportion.xs.contentcols}
                >
                    <Input className={Style.input}
                        type='text'
                        value={name}
                        onChange={handleNameChange}
                        maxLength={10}
                        required={true}
                        validationMessage="此為必填，請輸入不超過10字元的內容"
                    />
                </StyledGridBorder>

                <StyledTitleGridBorder
                    item={true}
                    sm={GridProportion.sm.titlecols}
                    xs={GridProportion.xs.titlecols}
                >
                    <p>
                        <span style={{ color: "red" }}>*</span>
                        所屬
                    </p>
                </StyledTitleGridBorder>
                <StyledGridBorder
                    item={true}
                    sm={GridProportion.sm.contentcols}
                    xs={GridProportion.xs.contentcols}
                >
                    <DropDownList
                        data={["魏", "蜀", "吳"]}
                        value={belongs}
                        onChange={handleBelongsChange}
                        required={true}
                        validationMessage="此為必選，請選擇一個選項"
                    />
                </StyledGridBorder>

                <StyledTitleGridBorder
                    item={true}
                    sm={GridProportion.sm.titlecols}
                    xs={GridProportion.xs.titlecols}
                >
                    <p>
                        <span style={{ color: "red" }}>*</span>
                        職位
                    </p>
                </StyledTitleGridBorder>
                <StyledGridBorder
                    item={true}
                    sm={GridProportion.sm.contentcols}
                    xs={GridProportion.xs.contentcols}
                >
                    <Input className={Style.input}
                        type='text'
                        value={position}
                        onChange={handlePositionChange}
                        maxLength={20}
                        required={true}
                        validationMessage="此為必填，請輸入不超過20字元的內容"
                    />
                </StyledGridBorder>

                <StyledGridBorder
                    item={true}
                    sm={GridProportion.sm.contentcols}
                    xs={GridProportion.xs.contentcols}
                >
                    <Input className={Style.input}
                        type='text'
                        value={position}
                        onChange={handlePositionChange}
                        maxLength={20}
                        required={true}
                        validationMessage="此為必填，請輸入不超過20字元的內容"
                    />
                </StyledGridBorder>

            </StyledGrid>
        </form>
    )
}

export class PageflowForm extends Component<IPageflowFormProps> {

    readonly state = {
        id: this.props.editDataItem.id || this.props.nextId,
        name: this.props.editDataItem.name || "",
        belongs: this.props.editDataItem.belongs || "",
        position: this.props.editDataItem.position || "",
    }

    // 定位表單
    form = React.createRef<HTMLFormElement>();

    public handleNameChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({
            name: event.target.value,
        });
    }

    public handleBelongsChange = (event: DropDownListChangeEvent) => {
        this.setState({
            belongs: event.target.value,
        });
    }

    public handlePositionChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({
            position: event.target.value,
        });
    }

    /**
     * Usage: 儲存處理函式，先檢查表單的資料合法性再送出表單
     * Param: none
     * As Callback: <Taskbar />
     */
    public handleFormSubmit = () => (event) => {
        if (this.form.current !== null) {

            if (this.form.current.reportValidity()) {
                this.handleSubmitClick();
            }
        }
    }

    public handleSubmitClick = () => {

        if (this.props.pageShow === "create") {

            const dataItem = {
                id: this.state.id,
                name: this.state.name,
                belongs: this.state.belongs,
                position: this.state.position
            }

            this.props.actions.insertGridData(dataItem);

        } else if (this.props.pageShow === "edit") {

            const dataItem = {
                id: this.state.id,
                name: this.state.name,
                belongs: this.state.belongs,
                position: this.state.position
            }

            this.props.actions.updateGridData(dataItem);

        } else {
            console.log("Pageflow_form handleSubmitClick pageShow error");
        }

        this.props.handleHomeChange();
    }

    render() {

        const { pageShow, handleHomeClick } = this.props;
        const { id, name, belongs, position } = this.state;

        let titleName = "範例新增";
        if (pageShow === "edit") {
            titleName = "範例編輯";
        }

        return (
            <div>
                <Taskbar
                    className={Style.taskbararea}
                    data={[
                        { name: '返回', onClick: handleHomeClick() },
                        { name: '儲存', onClick: this.handleFormSubmit() },
                    ]}
                />

                <Title title={titleName} />

                <InputForm
                    form={this.form}

                    id={id}
                    name={name}
                    belongs={belongs}
                    position={position}

                    handleNameChange={this.handleNameChange}
                    handleBelongsChange={this.handleBelongsChange}
                    handlePositionChange={this.handlePositionChange}
                />
            </div>
        )
    }
}

const mapStateToProps = state => ({
});

const mapDispatchToProps = (dispatch: Dispatch) => {
    const actions = {
        insertGridData,
        updateGridData,
    }

    return {
        actions: bindActionCreators(actions, dispatch),
        dispatch,
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(PageflowForm);
