import React, { useState } from 'react';
// css
const Style = require("./Popup_style.module.css");
// types
import { PopupKendoComponentProps } from './popup';
// shared component
import { StyledPopover } from "component/material/StyledPopup";
import { PopupTitle } from "component/title/Title";
import { StyledButtonCancel } from "component/material/StyledButton";
// material
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
// kendo
import { DatePicker } from "@progress/kendo-react-dateinputs";
import { DropDownList, ComboBox } from '@progress/kendo-react-dropdowns';

export const PopupKendoComponent: React.FC<PopupKendoComponentProps> = (props) => {

    const { open, handleOpenChange } = props;

    const [datepickerValue, setdatepickerValue] = useState<Date | null>(new Date());

    let el;

    return (
        <div>
            <StyledPopover
                open={open}
                style={{
                    background: 'rgba(0, 0, 0, 0.4)',
                    zIndex: 95
                }}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
                transformOrigin={{
                    vertical: 'center',
                    horizontal: 'center',
                }}
                disableEnforceFocus={true}
            >
                <div className={Style.listitem}>
                    <PopupTitle title="Popup with Kendo DatePicker and DropDownList Sample" />
                    <IconButton style={{ position: 'absolute', right: '12px', top: '0px' }}
                        onClick={handleOpenChange}
                    >
                        <CloseIcon />
                    </IconButton>
                </div>
                <div className={Style.popup_content}>
                    {/* 需在DatePicker新增ref和設定popupSetting的appendTo 
                        需在Popover設定disableEnforceFocus為true */}
                    <div ref={ele => (el = ele)}>
                        <p>DatePicker</p>
                        <DatePicker
                            popupSettings={{
                                appendTo: el
                            }}
                            value={datepickerValue}
                            onChange={(event) => setdatepickerValue(event.target.value)}
                        />
                    </div>
                    {/* DropDownList無需做特別的調整 */}
                    <div>
                        <p>DropDownList</p>
                        <DropDownList
                            data={[
                                '天氣之子',
                                '你的名字',
                                '言葉之庭',
                            ]}
                        />
                    </div>
                    {/* ComboBox無需做特別的調整 */}
                    <div>
                        <p>ComboBox</p>
                        <ComboBox
                            data={[
                                '追逐繁星的孩子',
                                '秒速五釐米',
                                '雲之彼端，約定的地方',
                            ]}
                        />
                    </div>

                    <div className={Style.popup_functionarea}>
                        <StyledButtonCancel
                            onClick={handleOpenChange}
                        >
                            關閉
                            </StyledButtonCancel>
                    </div>
                </div>
            </StyledPopover>
        </div>
    )
}

export default PopupKendoComponent;