import React, { Component } from 'react';
// css
const Style = require("./Form_style.module.css");
// shared component
import {
    StyledGrid,
    StyledTitleGridBorder,
    StyledGridBorder,
    GridProportion,
} from 'component/material/StyledGrid';
// material component
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
// kendo component
import { Input } from '@progress/kendo-react-inputs';
import { ComboBox, DropDownList, MultiSelect } from '@progress/kendo-react-dropdowns';
import {
    DatePicker,
    DatePickerChangeEvent,
    DateTimePicker,
    DateTimePickerChangeEvent,
} from '@progress/kendo-react-dateinputs';

const dataExample = ["華儲", "倉儲", "管理", "系統"];

export class SingleDataInput extends Component {

    readonly state = {
        date: new Date(),
        datetime: new Date()
    }

    handleDateChange = (event: DatePickerChangeEvent) => {

        const date = event.value;

        this.setState({
            date: date,
        })
    }

    handleDatetimeChange = (event: DateTimePickerChangeEvent) => {

        const datetime = event.value;

        this.setState({
            datetime: datetime,
        })
    }

    render() {

        const { datetime } = this.state;

        return (
            <div>
                <StyledGrid
                    container={true}
                >
                    <StyledTitleGridBorder
                        item={true}
                        sm={GridProportion.sm.titlecols}
                        xs={GridProportion.xs.titlecols}
                    >
                        <p>單筆輸入</p>
                    </StyledTitleGridBorder>
                    <StyledGridBorder
                        item={true}
                        sm={GridProportion.sm.contentcols}
                        xs={GridProportion.xs.contentcols}
                    >
                        <Input className={Style.input}
                            type='text'
                        />
                    </StyledGridBorder>

                    <StyledTitleGridBorder
                        item={true}
                        sm={GridProportion.sm.titlecols}
                        xs={GridProportion.xs.titlecols}
                    >
                        <p>選項按鈕</p>
                    </StyledTitleGridBorder>
                    <StyledGridBorder
                        item={true}
                        sm={GridProportion.sm.contentcols}
                        xs={GridProportion.xs.contentcols}
                    >
                        <FormControl>
                            <RadioGroup
                                style={{ display: 'inline' }}
                            >
                                <FormControlLabel value="Y" control={<Radio />} label="是" />
                                <FormControlLabel value="N" control={<Radio />} label="否" />
                            </RadioGroup>
                        </FormControl>
                    </StyledGridBorder>

                    <StyledTitleGridBorder
                        item={true}
                        sm={GridProportion.sm.titlecols}
                        xs={GridProportion.xs.titlecols}
                    >
                        <p>下拉選單</p>
                    </StyledTitleGridBorder>
                    <StyledGridBorder
                        item={true}
                        sm={GridProportion.sm.contentcols}
                        xs={GridProportion.xs.contentcols}
                    >
                        <DropDownList
                            className={Style.combobox}
                            data={dataExample}
                        />
                    </StyledGridBorder>

                    <StyledTitleGridBorder
                        item={true}
                        sm={GridProportion.sm.titlecols}
                        xs={GridProportion.xs.titlecols}
                    >
                        <p>可輸入下拉選單</p>
                    </StyledTitleGridBorder>
                    <StyledGridBorder
                        item={true}
                        sm={GridProportion.sm.contentcols}
                        xs={GridProportion.xs.contentcols}
                    >
                        <ComboBox
                            className={Style.combobox}
                            data={dataExample}
                        />
                    </StyledGridBorder>

                    <StyledTitleGridBorder
                        item={true}
                        sm={GridProportion.sm.titlecols}
                        xs={GridProportion.xs.titlecols}
                    >
                        <p>多選下拉選單</p>
                    </StyledTitleGridBorder>
                    <StyledGridBorder
                        item={true}
                        sm={GridProportion.sm.contentlongcols}
                        xs={GridProportion.xs.contentlongcols}
                    >
                        <MultiSelect
                            className={Style.combobox}
                            data={dataExample}
                        />
                    </StyledGridBorder>

                    <StyledTitleGridBorder
                        item={true}
                        sm={GridProportion.sm.titlecols}
                        xs={GridProportion.xs.titlecols}
                    >
                        <p>日期選擇器</p>
                    </StyledTitleGridBorder>
                    <StyledGridBorder
                        item={true}
                        sm={GridProportion.sm.contentlongcols}
                        xs={GridProportion.xs.contentlongcols}
                    >
                        <DatePicker
                            className={Style.datepicker}
                            value={datetime}
                            format="yyyy/MM/dd"
                            validationMessage="請輸入正確的時間格式"
                            onChange={this.handleDateChange}
                        />
                    </StyledGridBorder>

                    <StyledTitleGridBorder
                        item={true}
                        sm={GridProportion.sm.titlecols}
                        xs={GridProportion.xs.titlecols}
                    >
                        <p>日期時間選擇器</p>
                    </StyledTitleGridBorder>
                    <StyledGridBorder
                        item={true}
                        sm={GridProportion.sm.contentlongcols}
                        xs={GridProportion.xs.contentlongcols}
                    >
                        <DateTimePicker
                            className={Style.datepicker}
                            value={datetime}
                            format="yyyy/MM/dd HH:mm:ss"
                            validationMessage="請輸入正確的時間格式"
                            onChange={this.handleDatetimeChange}
                        />
                    </StyledGridBorder>

                </StyledGrid>
            </div>
        )
    }
}

export default SingleDataInput
