// react
import * as React from 'react';
// react router
// redux
// selector
// axios
// css、less、sass
const Style = require('./alert_style.module.css');
// shared component
// material component
// kendo component
// other
// presentational component
// container component

interface AlertBlockProps {
    alertMessage: string;
}

export const AlertBlock: React.FC<AlertBlockProps> = (props) => {
    return (
        <div
            className={Style.alert_message}
        >
            {props.alertMessage}
        </div>
    )
}