// Verify
import {
    SET_VERIFY_ITEM,
    SET_VERIFY_TRANSATIONID,
    SET_VERIFY_QUESTION,
} from './types';
// authentication token
import {
    SET_JWT_DATA
} from './types';
// Logging
import {
    SET_ISLOGGING
} from './types';
// System User Info
import {
    SET_USER_DATA,
    SET_USERMENU_DATA,
    SET_USERCOMPONENTTASK_DATA,
    SET_USERFAVORITE_DATA,
    SET_USERLASTLOGINTIME_DATA,
} from './types';

// Verify
export interface SetVerifyItem {
    type: SET_VERIFY_ITEM,
    item: [],
}

export interface SetVerifyTransationId {
    type: SET_VERIFY_TRANSATIONID,
    transationId: string,
}

export interface SetVerifyQuestion {
    type: SET_VERIFY_QUESTION,
    question: string,
}

export type VerifyItem = SetVerifyItem |
    SetVerifyTransationId |
    SetVerifyQuestion;

export const setVerifyItem: (item: []) => SetVerifyItem =
    (item) => ({
        type: SET_VERIFY_ITEM,
        item
    });

export const setVerifyTransationId: (transationId: string) => SetVerifyTransationId =
    (transationId) => ({
        type: SET_VERIFY_TRANSATIONID,
        transationId
    });

export const setVerifyQuestion: (question: string) => SetVerifyQuestion =
    (question) => ({
        type: SET_VERIFY_QUESTION,
        question
    });

// authentication token
export interface SetJwtData {
    type: SET_JWT_DATA,
    Data: string,
}

export const setJwtData: (Data: string) => SetJwtData =
    (Data) => ({
        type: SET_JWT_DATA,
        Data
    });

// Logging
export interface SetIsLogging {
    type: SET_ISLOGGING,
    islogging: boolean,
}

export const setIsLogging: (isLogging: boolean) => SetIsLogging =
    (islogging) => ({
        type: SET_ISLOGGING,
        islogging
    });

// System User Info
export interface SetUserData {
    type: SET_USER_DATA,
    Userid: string,
    Username: string,
}

export const setUserData: (Userid: string, Username: string) => SetUserData =
    (Userid, Username) => ({
        type: SET_USER_DATA,
        Userid,
        Username
    });

export interface SetUserMenuData {
    type: SET_USERMENU_DATA,
    Data: any,
}

export const setUserMenuData: (Data: any) => SetUserMenuData =
    (Data) => ({
        type: SET_USERMENU_DATA,
        Data
    });

export interface SetUserComponentTaskData {
    type: SET_USERCOMPONENTTASK_DATA,
    Data: any,
}

export const setUserComponentTaskData: (Data: any) => SetUserComponentTaskData =
    (Data) => ({
        type: SET_USERCOMPONENTTASK_DATA,
        Data
    });

export interface SetUserFavoriteData {
    type: SET_USERFAVORITE_DATA,
    Data: any,
}

export const setUserFavoriteData: (Data: any) => SetUserFavoriteData =
    (Data) => ({
        type: SET_USERFAVORITE_DATA,
        Data
    });

export interface SetUserLastLoginTimeData {
    type: SET_USERLASTLOGINTIME_DATA,
    Data: string,
}

export const setUserLastLoginTimeData: (Data: string) => SetUserLastLoginTimeData =
    (Data) => ({
        type: SET_USERLASTLOGINTIME_DATA,
        Data
    });
