// react
import React from "react";
// react route
import { NavLink } from "react-router-dom";
// redux
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { setSidebarlistItem } from "reducers/Layout/action";
// selector
import { getUserLastLoginTimeData } from "reducers/Login/selector";
import { getUserMenuLayoutData } from "reducers/Layout/selector";
// axios
// css less sass
import Style from "./style.module.css";
// shared component
import { DatetimeFormat } from "component/utility/Timestamp";
// material component
import Hidden from "@material-ui/core/Hidden";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
// kendo component
// other
// presentational component
// container component
// temp datasource
import twmsMenuRoutes from "routes/twmsMenuRoutes";
import { getUsername } from "reducers/Login/selector";

const MobileHomepageList = props => {
    return (
        <Hidden smUp>
            <div className="homepageheader">
                <div className="homepageheaderinfo">
                    <h2
                        style={{
                            display: "block",
                            fontWeight: "bolder",
                            color: "#a38f6a",
                            margin: "0px",
                            paddingLeft: "1.5rem",
                            paddingTop: "1rem"
                        }}
                    >
                        華儲倉儲管理系統 UI_Pattern
                        <p style={{ fontSize: "20px", fontWeight: "normal", color: "#333333", marginTop: "10px" }}>
                            親愛的 OOO，您好
                        </p>
                        <div style={{ fontSize: "16px", fontWeight: "normal", color: "#333333" }}>
                            上次登入時間：yyyy/MM/DD hh:mm:ss
                        </div>
                    </h2>
                </div>
            </div>
        </Hidden>
    );
};

const WindowsHomepageList = props => {
    return (
        <div className="homepageheader">
            <div className="homepageheaderinfo">
                <h2
                    style={{
                        display: "block",
                        fontWeight: "bolder",
                        color: "#a38f6a",
                        margin: "0px",
                        paddingLeft: "1.5rem",
                        paddingTop: "1rem"
                    }}
                >
                    華儲倉儲管理系統 UI_Pattern
                    <p style={{ fontSize: "20px", fontWeight: "normal", color: "#333333", marginTop: "10px" }}>
                        親愛的 OOO，您好
                    </p>
                    <div style={{ fontSize: "16px", fontWeight: "normal", color: "#333333" }}>
                        上次登入時間：yyyy/MM/DD hh:mm:ss
                    </div>
                </h2>
            </div>
        </div>
    );
};

const HomepageRWD = props => {
    const handleIconClick = sidebarListItem => event => {
        props.actions.setSidebarlistItem(sidebarListItem);
    };

    return (
        <div>
            <Hidden smDown>
                <WindowsHomepageList
                    systemList={props.systemList}
                    username={props.username}
                    userLastLoginTime={props.userLastLoginTime}
                    handleIconClick={handleIconClick}
                />
            </Hidden>
            <Hidden smUp>
                <MobileHomepageList
                    systemList={props.systemList}
                    username={props.username}
                    userLastLoginTime={props.userLastLoginTime}
                    handleIconClick={handleIconClick}
                />
            </Hidden>
        </div>
    );
};

const Homepage = props => {
    return (
        <HomepageRWD
            systemList={props.userMenuLayoutData}
            username={props.username}
            userLastLoginTime={props.userLastLoginTime}
            actions={props.actions}
        />
    );
};

const mapStateToProps = state => ({
    userMenuLayoutData: getUserMenuLayoutData(state),
    username: getUsername(state),
    userLastLoginTime: getUserLastLoginTimeData(state)
});

const mapDispatchToProps = dispatch => {
    const actions = {
        setSidebarlistItem
    };

    return {
        actions: bindActionCreators(actions, dispatch),
        dispatch
    };
};
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Homepage);
